HTML LessResult EDIT ON * {
  margin: 0px;
  padding: 0px;
  font-weight: 200;
  font-family: helvetica neue, helvetica, arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}

*:focus{
  outline: unset !important;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #000;
  text-decoration: none;
}

body {
  height: 100vh !important;
    overflow: hidden;
  background: #fafafa;
  position: fixed;
}

.main_container {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.container {
  width: 100%;
  position: fixed;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: rgb(255 255 255 / 52%); */
  -webkit-backdrop-filter: blur(91px);
  backdrop-filter: blur(91px);
  display: flex;
  flex-direction: column;
}

p {
  float: left;
  line-height: 50px;
  font-size: 200%;
  color: #888;
}

.scroller {
  height: 50px;
  line-height: 50px;
  float: left;
  margin-left: 5px;
  overflow: hidden;
}

.scroller span {
  display: block;
  font-size: 200%;
  color: #000;
}

.scroller .inner {
  -webkit-animation: scroll 11s 1 ease-out;
  -moz-animation: scroll 11s 1 ease-out;
  animation: scroll 11s 1 ease-out;
  -webkit-animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}


.blue {
  -webkit-animation: color 0.25s 1 linear;
  -moz-animation: color 0.25s 1 linear;
  animation: color 0.25s 1 linear;

  -webkit-animation-delay: 8s;
  -moz-animation-delay: 8s;
  animation-delay: 8s;

  -webkit-animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  animation-fill-mode: forwards;

}


.blueb {
  -webkit-animation: color 0.25s 1 linear;
  -moz-animation: color 0.25s 1 linear;
  animation: color 0.25s 1 linear;

  -webkit-animation-delay: 8.25s;
  -moz-animation-delay: 8.25s;
  animation-delay: 8.25s;

  -webkit-animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}


